.frame {
  place-items: center;
  place-content: center;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 0.75rem;
  margin: 0;
  transition-property: color, background-color;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
  display: grid;
  grid-template-rows: calc(100% - 2rem) auto;
}
.frame.do {
  background: #cfffc8;
  color: #1dc502;
}
.frame.dont {
  background: #ffcaca;
  color: #e11d1d;
}

img {
  object-fit: contain;
  overflow: hidden;
  max-width: 100%;
  max-height: 100%;
}

figcaption {
  line-height: 1.5;
}

small {
  opacity: 0.5;
}

/*  */

html,
body,
#root {
  display: flex;
  place-items: center;
  place-content: center;
  text-align: center;
  padding: 0;
  margin: 0;
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

* {
  box-sizing: border-box;
}
